import { Injectable } from '@angular/core';
import { subject } from './decorator';
import { Observable } from 'rxjs';

/**
 * æ¶æ¯åéæå¡ç±»
 * @author xuchao
 */
@Injectable({
  providedIn: 'root'
})
export class AbMessageQueueService {

  constructor() { }

  /**
   * æ¶æ¯åéæ¹æ³
   * @param type æ¶æ¯ç±»å
   * @param value æ¶æ¯æºå¸¦å¼
   */
  public send(type: string, value?: any) {
    subject.next({ type, value });
  }

  /**
   * å°æ¶æ¯å¤çæ¹æ³ç»å®å°å¯¹åºå®ä½ä¸
   * @param context componentå®ä½
   */
  public bind(context: any) {
    const queue = Reflect.getMetadata('ab:message', context) as Observable<any>;
    const handler = Reflect.getMetadata('ab:handler', context);
    if (queue) {
      queue.subscribe(val => {
        const callback = handler[val.type];
        if (callback) {
          callback.call(context, val.value);
        }
      });
    }
  }
}
