import 'reflect-metadata';
import { Subject } from 'rxjs';

export const subject = new Subject<any>();

/**
 *
 * @param type æ¶æ¯ç±»åï¼å¤ä¸ªç±»åä»¥,åé
 * @example
 *      @MessageHandler('test')
 *      handlerTest(value: any) {
 *        // ...
 *      }
 */
export const MessageHandler = function (type: string) {
  return function (target: any, key: string, descriptor: any) {
    if (/(\w+\,)*\w+/.test(type)) {
      const types = type.split(',');
      let map = Reflect.getMetadata('ab:handler', target);
      if (!map) {
        map = new Map<String, Function>();
      }
      types.forEach((t: string) => {
        map[t] = descriptor.value;
      });
      Reflect.defineMetadata('ab:message', subject.asObservable(), target);
      Reflect.defineMetadata('ab:handler', map, target);
    } else {
      throw new Error('ç±»åä»éå­æ¯æ°å­ä¸åçº¿');
    }
  };
};

